.App {
    padding: 20px 0;
}

.GoodsList .button {
    margin-top: 40px;
    width: 20%;
}

.GoodsList .header {
    font-size: 24px;
}

.card > .header, .content {
    cursor: text;
}

.card > .button {
    position: absolute;
    opacity: .8;
}

.card > .button:hover {
    opacity: 1;
    transition: .2s ease;
    -webkit-transition: .2s ease;
    -moz-transition: .2s ease;
    -ms-transition: .2s ease;
    -o-transition: .2s ease;
}


.modal {
    position: fixed;
    z-index: 5;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(0,0,0,0.5);
    justify-content: center;
    align-items: center;
}

.modal-content {
    width: 40%;
    background-color: white;
    padding: 20px;
    position: relative;
}

.modal-content .icon {
    position: absolute;
    top: 5px;
    right: 0;
    cursor: pointer;
}